<template>
	<div id="takeout-timelimit">
		<public-header title="时限要求"></public-header>
		<div class="content">
			<van-cell-group>
				<van-cell>
					<div slot="title">支付时间限制</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input type="number" v-model="order.pay_time_limit"><span>分</span>
						</div>
					</div>
				</van-cell>
				<van-cell>
					<div slot="title">未支付提醒</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input type="number" v-model="order.pay_time_notice"><span>分</span>
						</div>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title">商家接单时间限制</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input type="number" v-model="order.handle_time_limit"><span>分</span>
						</div>
					</div>
				</van-cell>
				<van-cell>
					<div slot="title">配送员接单时间限制</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input type="number" v-model="order.deliveryer_collect_time_limit"><span>分</span>
						</div>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title">外卖订单自动完成(已接单订单)</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input type="number" v-model="order.auto_success_hours"><span>分</span>
						</div>
					</div>
				</van-cell>
				<van-cell>
					<div slot="title">店内订单自动完成(已接单订单)</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input type="number" v-model="order.tangshi_auto_success_hours"><span>分</span>
						</div>
					</div>
				</van-cell>
			</van-cell-group>
			<van-radio-group class="margin-10-t">
				<van-cell-group>
					<van-cell title="订单标记" />
					<van-cell class="border-0px">
						<div slot="title">订单完成超时(支付完成到订单完成)</div>
						<div slot="right-icon">
							<div class="input-unit">
								<input type="number" v-model="order.timeout_limit"><span>分</span>
							</div>
						</div>
					</van-cell>
					<van-cell class="border-0px">
						<div slot="title">订单配送提前(配送员接单到订单完成)</div>
						<div slot="right-icon">
							<div class="input-unit">
								<input type="number" v-model="order.delivery_before_limit"><span>分</span>
							</div>
						</div>
					</van-cell>
					<van-cell class="border-0px">
						<div slot="title">订单配送超时(配送员接单到订单完成)</div>
						<div slot="right-icon">
							<div class="input-unit">
								<input type="number" v-model="order.delivery_timeout_limit"><span>分</span>
							</div>
						</div>
					</van-cell>
					<van-cell class="border-0px">
						<div slot="title">待抢订单超时<br>(商家通知配送员接单到配送员接单)</div>
						<div slot="right-icon">
							<div class="input-unit">
								<input type="number" v-model="order.delivery_status_3.timeout_limit"><span>分</span>
							</div>
						</div>
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<div class="padding-15">
				<van-button size="normal" class="bg-primary font-16" block @click="onSubmit">提交</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				showPreLoading: true,
				order:{
					pay_time_limit: '0',
					pay_time_notice: '0',
					handle_time_limit: '0',
					deliveryer_collect_time_limit: '0',
					auto_success_hours: '0',
					timeout_limit: '0',
					delivery_before_limit: '0',
					delivery_timeout_limit: '0',
					delivery_status_3:{
						timeout_limit: '0',
					}
				},
			}
		},
		components: {
			publicHeader
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: this,
					autoAssign: true,
					variable: 'order',
					url: 'plateform/config/takeout/order'
				});
			},

			onSubmit(){
				var that = this;
				setData({
					vue: this,
					url: 'plateform/config/takeout/order',
					data: that.order
				});
			}
		},

		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	
</style>


